export type InputType = "tel" | "email";

type ContactItemLinkProps = {
  type?: InputType;
  children: any; // TODO: ReactNode causes issues with ContactItemLink. Need to resolve.
};

export const ContactItemLink = ({
  type,
  children,
}: ContactItemLinkProps): JSX.Element => {
  if (!type) return children;
  return (
    <a
      href={`${type}:${children}`}
      style={{ color: "#fff", textDecoration: "underline" }}
    >
      {children}
    </a>
  );
};
