import "photoswipe/dist/photoswipe.css";
import ReactDomServer from "react-dom/server";
import { Grid as Container, Row, Col } from "react-bootstrap";
import { Gallery as PhotoswipeGallery, Item } from "react-photoswipe-gallery";
import "./styles.css";

// asset imports (🤮 but it works and we'll most likely implement a backend/CMS before changing the count)
import gallery1 from "./assets/01";
import gallery2 from "./assets/02";
import gallery3 from "./assets/03";
import gallery4 from "./assets/04";
import gallery5 from "./assets/05";
import gallery6 from "./assets/06";
import gallery7 from "./assets/07";
import gallery8 from "./assets/08";
import gallery9 from "./assets/09";

type GalleryItem = {
  original: string;
  thumbnail: string;
  thumbWidth: number;
  thumbHeight: number;
  width: number;
  height: number;
  caption?: string;
};

const items: GalleryItem[] = [
  gallery1,
  gallery2,
  gallery3,
  gallery4,
  gallery5,
  gallery6,
  gallery7,
  gallery8,
  gallery9,
];

const Caption = ({ text }: { text: string }): JSX.Element => (
  <Container>
    <Row>
      <p className="gallery-caption">{text}</p>
    </Row>
  </Container>
);

export const Gallery = (): JSX.Element => (
  <div id="portfolio" className="text-center bd-pink-bg">
    <Container>
      <div className="section-title">
        <h2>Gallery</h2>
        <p>Some of our most recent work</p>
      </div>
      <Row>
        <div className="portfolio-items">
          <PhotoswipeGallery withCaption>
            {items.map(
              (item: GalleryItem, i: number): JSX.Element => (
                <Col sm={6} md={4} lg={4} key={`photo-${i}`}>
                  <div className="portfolio-item">
                    <Item
                      original={item.original}
                      thumbnail={item.original}
                      width={item.width}
                      height={item.height}
                      cropped
                      // FYI: caption can be a string, _including_ an HTML string, _however_,
                      // it _cannot_ be an actual ReactNode or JSX.Element. According to the
                      // maintainers, this is on their roadmap. In the meantime, we'll use the
                      // DOM server to render the HTML string, allowing us to use native
                      // componets to build and style the caption.
                      caption={
                        item.caption &&
                        ReactDomServer.renderToStaticMarkup(
                          <Caption text={item.caption} />
                        )
                      }
                    >
                      {({
                        ref,
                        open,
                      }: {
                        ref: any;
                        open: any;
                      }): JSX.Element => (
                        <img
                          ref={ref}
                          onClick={open}
                          src={item.thumbnail}
                          width={item.thumbWidth}
                          height={item.thumbHeight}
                          alt={item.caption}
                          className="img-responsive"
                        />
                      )}
                    </Item>
                  </div>
                </Col>
              )
            )}
          </PhotoswipeGallery>
        </div>
      </Row>
    </Container>
  </div>
);
