import { ReactNode } from "react";
import { Grid as Container, Row, Col } from "react-bootstrap";
import "./styles.css";
import blueImg from "./assets/blue.webp";
import tullyImg from "./assets/tully.webp";

type TeamMemberProps = {
  name: string;
  title: string;
  image: string;
  children: ReactNode;
} & ReactBootstrap.Col.ColProps;
const TeamMember = ({
  name,
  title,
  image,
  children,
  ...props
}: TeamMemberProps): JSX.Element => (
  <Col {...props} className="team">
    <div className="thumbnail">
      <img
        src={image}
        alt={`${name} - ${title}`}
        className="team-img"
        width={240}
        height={240}
      />
      <div className="caption">
        <h3>{name}</h3>
        <p>{title}</p>
      </div>
      {children && <p>{children}</p>}
    </div>
  </Col>
);

export const Team = (): JSX.Element => (
  <div id="team" className="text-center bd-pink-bg">
    <Container>
      <Col md={8} mdOffset={2} className="section-title">
        <h2>Meet the Team</h2>
      </Col>
      <Row>
        <TeamMember name="Blue" title="Founder" image={blueImg} sm={6}>
          The Blue Dog is an almost 8 year old American Bully. In his free time,
          he loves chasing tennis balls (and then immediately destroying them),
          playing tug of war (or trying to tear your rotator cuff, we're not
          sure), and cuddling under a nice, warm blanket. Blue was the first dog
          Savannah ever trained, and inspired her journey into helping others
          overcome their relationship issues with their dogs. Now he is (mostly)
          retired, although he does occasionally stop in and say hi when
          Savannah needs a distraction during her training sessions!
        </TeamMember>
        <TeamMember
          name="Tully"
          title="CBO (Chief Ball Officer)"
          image={tullyImg}
          sm={6}
        >
          Tully is a 5 year old Belgian Malinois. Tully's only thought at any
          given time is "ball?" She is a very handler-oriented gal, and is part
          time demo dog, part time sport dog, and part time couch potato. She
          has achieved her PDC and her only second thought after ball is
          "decoy?" She's a hoot to work with, and was obviously the perfect hire
          for our CBO (Chief Ball Officer) position. She has managed to keep
          track of every ball she's ever crossed paths with, and is an
          invaluable asset to the Blue Dog Team.
        </TeamMember>
      </Row>
    </Container>
  </div>
);
