import { Grid as Container, Col, Row } from "react-bootstrap";
import "./styles.css";
import { SocialMediaListItem } from "./components/SocialMediaListItem";
import { ContactItem } from "./components/ContactItem";
import { Partners } from "./components/Partners";
import links from "./assets/links.json";

const calendlyStyling = {
  background_color: "fbeaeb",
  text_color: "000",
  primary_color: "2f3c7e",
};

export const Contact = (): JSX.Element => {
  const calendlyUrl = new URL(links.calendly);
  Object.entries(calendlyStyling).forEach(([k, v]) =>
    calendlyUrl.searchParams.set(k, v)
  );
  return (
    <div id="contact">
      <Container>
        <Row>
          <Col md={8}>
            <div className="section-title">
              <h2>Get In Touch</h2>
              <p>
                Please schedule a consultation, or if there are any questions,
                please reach out to us at one of the contact options below!
              </p>
            </div>
            <div className="text-center">
              <a
                href={calendlyUrl.href}
                target="_blank"
                rel="noreferrer"
                className="btn btn-custom btn-lg"
              >
                Schedule an In-Home Consultation
              </a>
            </div>
          </Col>
          <Col md={3} mdOffset={1}>
            <h3>Contact Info</h3>
            <ContactItem
              title="Location"
              info="Servicing Cabarrus and surrounding counties"
              iconClass="fa-map-marker"
            />
            <ContactItem
              title="Phone"
              info="(619) 601-1174"
              iconClass="fa-phone"
              type="tel"
            />
            <ContactItem
              title="Email"
              info="bluedogpettraining@gmail.com"
              iconClass="fa-envelope-o"
              type="email"
            />
          </Col>
        </Row>
        <Partners />
        <Col md={12}>
          <div className="social">
            <ul>
              <SocialMediaListItem
                url={links.facebook}
                iconClass="fa-facebook"
                label="View our Facebook page!"
              />
              <SocialMediaListItem
                url={links.instagram}
                iconClass="fa-instagram"
                label="View our Instagram page!"
              />
              <SocialMediaListItem
                url={links.thumbtack}
                iconClass="fa-thumb-tack"
                label="View our Thumbtack page!"
              />
            </ul>
          </div>
        </Col>
      </Container>
    </div>
  );
};
