import { ContactItemLink, InputType } from "./ContactItemLink";

type ContactItemProps = {
  title: string;
  info: string;
  iconClass: string;
  type?: InputType;
};

export const ContactItem = ({
  title,
  info,
  iconClass,
  type,
}: ContactItemProps): JSX.Element => (
  <div className="contact-item">
    <p>
      <span>
        <i className={`fa ${iconClass}`}></i> {title}
      </span>
      <ContactItemLink type={type}>{info}</ContactItemLink>
    </p>
  </div>
);
