type SocialMediaListItemProps = {
  url: string;
  iconClass: string;
  label: string;
};

export const SocialMediaListItem = ({
  url,
  iconClass,
  label,
}: SocialMediaListItemProps): JSX.Element => (
  <li>
    <a href={url} target="_blank" rel="noreferrer" aria-label={label}>
      <i className={`fa ${iconClass}`}></i>
    </a>
  </li>
);
